import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { Input } from '../../../commanComponent'

const InstallmentTable = (props) => {
    const {  installmentData, setInstallmentData,loanData } = props
    const installmentFields = [
        { key: 'installment', label: 'Installment' },
        { key: 'date', label: 'Date' },
        { key: 'amount', label: 'Amount' },
    ]

    const calculateEMIAfterChanges = (amount, index) => {
        let temp = installmentData;
        temp[index]['amount'] = parseFloat(amount).toFixed(2);
        let remEMI;
        let totalEMI = 0;
        temp.forEach((data, index1) => {
          totalEMI = totalEMI + parseFloat(data['amount']);
          if (index1 === index) {
            remEMI =
              parseFloat(loanData?.amount) - totalEMI >= 0
                ? parseFloat(loanData?.amount) - totalEMI
                : 0;
            remEMI = remEMI / (temp.length - 1 - index).toFixed(2);
          } else if (index1 > index) {
            data['amount'] = JSON.stringify(remEMI);
          }
          return data;
        });
    
        setInstallmentData(temp);
      };
    const installmentScopedSlots = useMemo(() => ({
        amount: (item, index) => (
            <span> 
                <Input
                type="number"
                className="form-control border border-black text-center"
                id="instl_amt"
                placeholder="Installment Amount"
                onChange={e =>
                    calculateEMIAfterChanges(e.target.value, index)
                }
                value={parseFloat(item?.amount)}
            />
            </span>
        ),
    }), [installmentData]);
    return (
        <React.Fragment>
            <div className='text-center'>
            <CustomTable
                data={installmentData}
                fields={installmentFields}
                // isLoading={isLoading}
                pagination={true}
                // currentPage={currentPage}
                // setCurrentPage={setCurrentPage}
                // totalPages={depList?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none",marginTop:"0px !important" }}
                scopedSlots={installmentScopedSlots}
            />
            </div>
        </React.Fragment>
    )
}

export default InstallmentTable
