import React, { useMemo } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { useCustomTranslation } from '../../../../translation'
import CustomTable from '../../../reuseableComponents/customTable/CustomTable'

const Arrears = (props) => {
    const { toggleAddArrearsModal } = props
    const { t } = useCustomTranslation()

    const arrearsEmployeeFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'Employee Name', label: 'Employee Name' },
        { key: 'emp_id', label: 'Employee ID' },
        { key: 'Year-month', label: 'Year-month' },
        { key: 'present_days', label: 'Present Days' },
        { key: 'pay_days', label: 'Pay Days' },
        { key: 'basic', label: 'Basic' },
        { key: 'hra', label: 'HRA' },
        { key: 'education_allowance', label: 'Education Allowance' },
        { key: 'meal_allowance', label: 'Meal Allowance' },
        { key: 'petrol_allowance', label: 'Petrol Allowance' },
        { key: 'special_allowance', label: 'Special Allowance' },
        { key: 'lta', label: 'LTA ' },
        { key: 'arrear', label: 'Arrear' },
        { key: 'pf_contribution', label: 'PF-Employee Contribution' },
        { key: 'esi_contribution', label: 'ESI Employee Contribution' },
        { key: 'action', label: 'Action' }
    ];
    const arrearsEmployeeScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),


    }), []);
    return (
        <div>
            <div className="d-flex justify-content-between align-items-center mb-2">
                <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Arrears</h3>
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() => toggleAddArrearsModal()}
                    text={"Add Arrear"}
                />
            </div>
            <div className='card p-3'>
              
                <div className="row">
                    <div className="col-12 form-group mb-3">
                        <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Employees</h3>
                        <CustomTable
                            data={[]}
                            fields={arrearsEmployeeFields}
                            // isLoading={isLoading}
                            pagination={true}
                            // currentPage={currentPage}
                            // setCurrentPage={setCurrentPage}
                            // totalPages={depList?.totalPages}
                            // tableName={""}
                            // onSearch={handleSearch}
                            isHeader={false}
                            tableStyle={{ padding: "0px", boxShadow: "none" }}
                            scopedSlots={arrearsEmployeeScopedSlots}
                        />

                    </div>
                </div>

            </div>
            <CustomButton
                classNames={"btn btn-primary gradientBg mt-2"}
                // handleClick={() => ()}
                text={"Save"}
            />
        </div>
    )
}

export default Arrears
