import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { getBadgeColor } from '../../../../utils/common';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { payrollActions } from '../../../../core/resource/actions';

const EmpAttendanceTrackingTable = (props) => {
    const dispatch =useDispatch()    
    const {currentPage,setCurrentPage ,getAllEmployeeDetailsByPayDays,monthYear} = props
    const {
        getEmployeeDetailsByPayDaysPending,
    } = useSelector(state => state.payroll)
    // const selectedEmployeeAttendanceDetail =(id)=>{
    //     navigate()
    // }
    const employeeField = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Emp Id' },
        { key: 'name', label: 'Full Name' },
        { key: 'present_days', label: 'Present Days' },
        { key: 'absent_days', label: 'Absent Days' },
        { key: 'present_days', label: 'Pay Days' },
        { key: 'account_status', label: 'Account Status' },
        { key: 'action', label: 'Action' }
    ];
    const setSelectedEmpData =(data)=>{
        dispatch(payrollActions.selectedEmployeeData(data))
    }
    
    const employeeScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        account_status: item =>(
            <span
            className={`badge ${getBadgeColor(item.account_status)}`}
          >
            {item.account_status}
          </span>
        ) ,
        

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <Link className="dropdown-item" to={`employee-attendance-tracking/${item?._id}/${monthYear}`}
                        onClick={() => setSelectedEmpData(item)} 
                        >
                            View Details</Link>
                    </li>
                    {/* <li>
                        <span className="dropdown-item"
                        // onClick={() => handleCreateUpdateEmployeeModal()}
                        >Update</span>
                    </li> */}
                </ul>
            </div>
        )
    }), [getAllEmployeeDetailsByPayDays,currentPage]);
    return (
        <>
            <CustomTable
                data={getAllEmployeeDetailsByPayDays?.data}
                fields={employeeField}
                isLoading={getEmployeeDetailsByPayDaysPending}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={getAllEmployeeDetailsByPayDays?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={employeeScopedSlots}
            />
        </>
    )
}

export default EmpAttendanceTrackingTable
