import React from 'react'
import { Link } from 'react-router-dom'

const RunAttendance = (props) => {
    const {toggleCreatePayRunModal}=props
    return (
        <React.Fragment>
            <div className=" d-flex justify-content-between align-items-center mb-2">
                <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Run Attendance</h3>
                
            </div>
            <div className='card'>
            <div className=" payroll-card-body-css  p-3 border-left border-dark">
                <div className="payruns-cards d-flex align-items-center justify-content-around">
                    <div className="col">
                        <small className="text-secondary">PAYMENT DATE</small>
                        <h5 className="py-3">08-09-2024</h5>
                    </div>
                    {/* { */}
                        {/* //   payRollNotification?.status === 'Ready' ?  */}
                        {/* // ( */}
                            <div className="col">
                                <button
                                    className="btn btn-primary"
                                // onClick={createPayRunId}
                                >
                                    Add Pay Run
                                </button>
                            </div>
                        {/* // )
                            //   : 
                            // ( */}
                                <div className="col">
                                    <button
                                        className="btn btn-primary"
                                    onClick={()=>toggleCreatePayRunModal()}
                                    >
                                        Create Pay Run
                                    </button>
                                    &nbsp;&nbsp;
                                    <Link to="/regular-payroll">
                                    <button className="btn btn-primary"
                                    //   onClick={createPayRun}
                                    >
                                        View Details
                                    </button>
                                    </Link>
                                </div>
                            {/* // )} */}
                </div>
                <i class="bi bi-info-circle"></i>
                <span>
                    <small className="text-secondary">
                        Please approve this payroll before{' '}
                        08-09-2024 | 04:00 PM
                    </small>
                </span>
            </div>
            </div>

        </React.Fragment>
    )
}

export default RunAttendance
