import React, { useEffect, useState } from 'react'
import { CustomTabs } from '../../reuseableComponents';
import Arrears from './arrears/Arrears';
import AddArrears from './arrears/AddArrears';
import Loans from './loans/Loans';
import AddLoans from './loans/AddLoans';
import PayrollHistory from './payrollHistory/payrollHistory';
import RunAttendance from './RunAttendance.js/RunAttendance';
import AttendanceTracking from './attendanceTracking/AttendanceTracking';
import CreatePayrun from './RunAttendance.js/CreatePayrun';
import { useDispatch, useSelector } from 'react-redux';
import { useCustomTranslation } from '../../../translation';
import { departmentAction, designationAction, employeeActions, payrollActions } from '../../../core/resource/actions';
import { pagination } from '../../../utils/Constants.config';
import { use } from 'i18next';
import moment from 'moment';

const PayRuns = () => {
    const dispatch = useDispatch()
    const { t } = useCustomTranslation()
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    } = useSelector(state => state.department)
    const {
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentPending,
        getDesignationsByDepartmentError
    } = useSelector(state => state.designation)

    const {
        getAllEmployeePending,
        getAllEmployeeRes,
        getAllEmployeeError,
    } = useSelector(state => state.employee)

    const {

        getEmployeeDetailsByPayDaysPending,
        getEmployeeDetailsByPayDaysRes,
        getEmployeeDetailsByPayDaysError,

    } = useSelector(state => state.payroll)

    const [isOpenAddArrears, setIsOpenAddArrears] = useState(false)
    const [isOpenAddLoans, setIsOpenAddLoans] = useState(false)
    const [openLoanTypeModal, setOpenLoanTypeModal] = useState('')
    const [isOpenCreatePayRun, setIsOpenCreatePayRun] = useState(false)
    const [allEmployeeList, setAllEmployeeList] = useState('')
    const [allDepartments, setAllDepartments] = useState('')
    const [allDesignations, setAllDesignations] = useState('')
    const [getAllEmployeeDetailsByPayDays, setGetAllEmployeeDetailsByPayDays] = useState([])
    const [attendanceStatusData,setAttendanceStatusData ] = useState([])
    const [currentPage,setCurrentPage]=useState(1)



    const tableTabsRequest = [
        { name: "Run Attendance", onClickFun: (id) => getPayRunsData(id), id: "runAttendance" },
        { name: "Attendance Tracking", onClickFun: (id) => getPayRunsData(id), id: "attendanceTracking" },
        { name: "Add Arrears", onClickFun: (id) => getPayRunsData(id), id: "addArrears" },
        { name: "Loans", onClickFun: (id) => getPayRunsData(id), id: "loans" },
        { name: "Payroll History", onClickFun: (id) => getPayRunsData(id), id: "payRollHistory" },
    ];
    const getPayRunsData = (id) => {
        if(id==="attendanceTracking"){
            getEmployeeDetailsByPayDays()
        }

    }
    const toggleAddArrearsModal = () => {
        setIsOpenAddArrears(!isOpenAddArrears)
    }
    const toggleAddLoanModal = (id, type) => {
        if (type) {
            setOpenLoanTypeModal(type)
        }
        setIsOpenAddLoans(!isOpenAddLoans)
    }
    const toggleCreatePayRunModal = () => {
        setIsOpenCreatePayRun(!isOpenCreatePayRun)
    }

    useEffect(() => {
        getAllDepartments()
        getAllEmployee()
        
        // getAttendanceStatus()
    }, [])
    /* Get All Employee List*/
    const getAllEmployee = () => {
        dispatch(employeeActions.getAllEmployeeRequest())
    }
    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code === 200) {

                    setAllEmployeeList(getAllEmployeeRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);

    /* Get All Departments */
    const getAllDepartments = () => {
        if (!getDepartmentListRes?.data?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(getDepartmentListRes?.data)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    /* Department by Designation  */
    const handleDepartmentByDesignation = (id) => {
        const params = {
            department_id: id
        }
        dispatch(designationAction.getDesignationsByDepartmentRequest(params));
    }

    useEffect(() => {
        if (!getDesignationsByDepartmentPending) {
            if (getDesignationsByDepartmentError) {
                return;
            }
            if (getDesignationsByDepartmentRes) {
                if (getDesignationsByDepartmentRes?.code === 200) {
                    const designationResponse = getDesignationsByDepartmentRes?.data || []
                    setAllDesignations(designationResponse)
                }
            }
        }
    }, [
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentError,
        getDesignationsByDepartmentPending
    ]);


    /* Get Employee Details By Pay Days  */
    const getEmployeeDetailsByPayDays = (param) => {
        // const payload ={
        //     page: currentPage,
        //     limit: pagination.limit,
        //     month_year: moment().format("YYYY-MM"),
        //     ...param
        // }
        // dispatch(payrollActions.getEmployeeDetailsByPayDaysRequest(payload));
        dispatch(payrollActions.getEmployeeDetailsByPayDaysRequest());
    }

    useEffect(() => {
        if (!getEmployeeDetailsByPayDaysPending) {
            if (getEmployeeDetailsByPayDaysError) {
                return;
            }
            if (getEmployeeDetailsByPayDaysRes) {
                if (getEmployeeDetailsByPayDaysRes?.code === 200) {
                    const getEmployeeDetailsByPayDaysData = getEmployeeDetailsByPayDaysRes?.data
                    setGetAllEmployeeDetailsByPayDays(getEmployeeDetailsByPayDaysData)
                }
            }
        }
    }, [
        getEmployeeDetailsByPayDaysRes,
        getEmployeeDetailsByPayDaysError,
        getEmployeeDetailsByPayDaysPending
    ]);

     
    



    return (
        <React.Fragment>
            <div className="tab-pane fade show active" id="receivedFeedback">
                <div className="cardBox myRequest mb-3">
                    <div className="cardHead d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <img
                                src="images/kpi-guidline.svg"
                                alt=""
                                width={40}
                                className="me-2 fw-bold"
                            />
                            <div className="">
                                <h3> Pay Runs</h3>
                            </div>
                        </div>
                        <div className="searchB me-0 ">
                            <input
                                type="text"
                                className="form-control w-100 fs-7"
                                placeholder="Search"
                            />
                        </div>
                    </div>
                    <CustomTabs tabs={tableTabsRequest} type="rayRuns" />
                    <div className="tab-content mykpiMain">
                        <div className="tab-pane fade active show" id="runAttendance">
                            <RunAttendance
                                toggleCreatePayRunModal={toggleCreatePayRunModal}
                            />
                        </div>
                        <div className="tab-pane fade" id="attendanceTracking">
                            <AttendanceTracking
                                allEmployeeList={allEmployeeList}
                                allDepartments={allDepartments}
                                allDesignations={allDesignations}
                                handleDepartmentByDesignation={handleDepartmentByDesignation}
                                getAllEmployeeDetailsByPayDays={getAllEmployeeDetailsByPayDays}
                                getEmployeeDetailsByPayDays={getEmployeeDetailsByPayDays}
                                attendanceStatusData={attendanceStatusData}
                            />
                        </div>
                        <div className="tab-pane fade" id="addArrears">
                            <Arrears
                                toggleAddArrearsModal={toggleAddArrearsModal}
                            />
                        </div>
                        <div className="tab-pane fade" id="loans">
                            <Loans
                                toggleAddLoanModal={toggleAddLoanModal}
                            />

                        </div>
                        <div className="tab-pane fade" id="payRollHistory">
                            <PayrollHistory

                            />
                        </div>
                    </div>
                </div>
            </div>
            {
            isOpenAddArrears && <AddArrears
                isOpen={isOpenAddArrears}
                toggleOpenClose={toggleAddArrearsModal}
                allEmployeeList={allEmployeeList}
            />}
            {
            isOpenAddLoans &&
                <AddLoans
                    isOpen={isOpenAddLoans}
                    toggleOpenClose={toggleAddLoanModal}
                    openLoanTypeModal={openLoanTypeModal}
                />
            }
            {
                isOpenCreatePayRun && <CreatePayrun
                    isOpen={isOpenCreatePayRun}
                    toggleOpenClose={toggleCreatePayRunModal}
                />
            }
        </React.Fragment>
    )
}

export default PayRuns
