import React, { useMemo, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { useCustomTranslation } from '../../../../translation'
import { CustomTable } from '../../../reuseableComponents'
import { Input, Label, Textarea } from '../../../commanComponent'
import CustomSelect from '../../../commanComponent/CustomSelect'

const AddArrears = (props) => {
    const { isOpen, toggleOpenClose, allEmployeeList } = props
    const { t } = useCustomTranslation()
    const [payDays, setPayDays] = useState('')
    const [errors, setErrors] = useState({})
    const [selectedEmployees, setSelectedEmployees] = useState('')
    const [arrearFormData, setArrearFormData] = useState({
        arrearName: '',
        arrearFor: '',
        payMonth: '',
        description: '',
        employees: [],

    })
    const addArrearsFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'pay_day', label: 'Pay Days' },
        { key: 'emp_code', label: 'Emp Code' },
        { key: 'name', label: 'Emp Name' },
        { key: 'department_name', label: 'Department  ' },
        { key: 'designation_name', label: 'Designation' },
    ];

    const addEmployeesPayDays = (e,item) => {

    }

    const addArrearsScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        pay_day: (item, index) => (
            <span>
                <Input
                    type="number"
                    value={payDays || ''}
                    name="pay_days"
                    onChange={(e) => addEmployeesPayDays(e, item)}
                />
            </span>
        ),


    }), []);
    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setArrearFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });
        if ("employees" == name) {
            setSelectedEmployees(value)
        }
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };
    return (<>
        <div className={`offcanvas offcanvas-end creatNewTicketOff ${isOpen ? 'show' : ''}`} tabIndex={-1}
            style={{ visibility: isOpen ? 'visible' : 'hidden', width: "800px" }}
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> Add Arrears </h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => toggleOpenClose()}
                />
            </div>

            <div className="offcanvas-body">
                <div className="formPanel">
                    <div className="row">
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.arrearName")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="form-group mb-3">
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.arrearName")}
                                    value={arrearFormData?.arrearName || ''}
                                    name="arrearName"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.arrearName && (
                                    <CustomError text={errors.arrearName} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.arrearFor")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="form-group mb-3">
                                <Input
                                    type="month"
                                    value={arrearFormData?.arrearFor || ''}
                                    name="arrearFor"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.arrearFor && (
                                    <CustomError text={errors.arrearFor} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.payMonth")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="form-group mb-3">
                                <Input
                                    type="month"
                                    value={arrearFormData?.payMonth || ''}
                                    name="payMonth"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.payMonth && (
                                    <CustomError text={errors.payMonth} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.description")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="form-group mb-3">
                                <Textarea
                                    name="description"
                                    value={arrearFormData?.description}
                                    placeholder={t("placeholders.description")}
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.description && (
                                    <CustomError text={errors.description} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.employee")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="form-group mb-3">
                                <CustomSelect
                                    data={allEmployeeList}
                                    placeholder={t("placeholders.employee")}
                                    value={arrearFormData?.employees || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "employees")}
                                    name="employees"
                                    isMulti={true}
                                />
                                {errors?.employees && (
                                    <CustomError text={errors.employees} />
                                )}
                            </div>
                        </div>
                    </div>


                    <CustomTable
                        data={selectedEmployees}
                        fields={addArrearsFields}
                        // isLoading={isLoading}
                        pagination={true}
                        // currentPage={currentPage}
                        // setCurrentPage={setCurrentPage}
                        // totalPages={depList?.totalPages}
                        // tableName={""}
                        // onSearch={handleSearch}
                        isHeader={false}
                        tableStyle={{ padding: "0px", boxShadow: "none" }}
                        scopedSlots={addArrearsScopedSlots}
                    />


                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={"Add"}
                            classNames={"btn btn-org-gradient"}
                        // handleClick={() => submitCreateUpdateDepartment()}
                        // isLoading={selectedDepartmentData ? updateDepartmentPending : addDepartmentPending}
                        />
                    </div>
                </div>
            </div>

        </div>
        {isOpen && <div className="modal-backdrop fade show"></div>}
    </>
    )
}

export default AddArrears


