import React, { useMemo, useState } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { useCustomTranslation } from '../../../../translation'
import { CustCheckbox, Label } from '../../../commanComponent'
import { CustomButton } from '../../../globalComponents'
import CustomSelect from '../../../commanComponent/CustomSelect'
const EmpData = [
    {
        emp_id: "qd2654",
        name: "Mohd Sarfaraz",
        email: "sarfaraz1@qdegrees.com",
        department: "IT",
        designation: "Junior Software Development"
    },
    {
        emp_id: "qd2281",
        name: "chandan kumar",
        email: "chandan@qdegrees.com",
        department: "IT",
        designation: "Junior Software Development"
    }
]
const CreatePayrun = (props) => {
    const { isOpen, toggleOpenClose } = props
    const { t } = useCustomTranslation()
    const [department, setDepartment] = useState('')

    const createPayRunFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'hold_salary', label: 'Hold Salary' },
        { key: 'create_salary', label: 'Create Salary' },
        { key: 'emp_id', label: 'Emp ID' },
        { key: 'name', label: 'Emp Name' },
        { key: 'email', label: 'Email' },
        { key: 'department', label: 'Department  ' },
        { key: 'designation', label: 'Designation' },
    ];

    const createPayRunScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        hold_salary: (item, index) => (
            <span>
                <CustCheckbox
                    key={"hold_salary"}
                    id={`hold_salary`}
                    // checked={isActive}
                    // handleChange={(e) => setIsActive(!isActive)}
                    name={"hold_salary"}
                />
            </span>
        ),
        create_salary: (item, index) => (
            <span>
                <CustCheckbox
                    key={"create_salary"}
                    id={`create_salary`}
                    // checked={isActive}
                    // handleChange={(e) => setIsActive(!isActive)}
                    name={"create_salary"}
                />
            </span>
        ),


    }), []);
    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${isOpen ? 'show' : ''}`} tabIndex={-1}
                style={{ visibility: isOpen ? 'visible' : 'hidden', width: "800px" }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> Create Pay Run </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => toggleOpenClose()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="row">
                        <div className="col-md-2">
                            <div className="mt-2">
                                <Label text={"Department"} className={"text-primary"}/>   
                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <CustomSelect
                                    data={[]}
                                    placeholder={"Select Department"}
                                    value={department || ''}
                                    handleChange={(selectedOption) => setDepartment(selectedOption)}
                                    name="department"
                                />


                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <CustCheckbox
                            key={"hold_salary"}
                            id={`hold_salary`}
                            // checked={isActive}
                            // handleChange={(e) => setIsActive(!isActive)}
                            name={"hold_salary"}
                            label={"Create Salary For All"}
                        />


                    </div>
                    <CustomTable
                        data={EmpData}
                        fields={createPayRunFields}
                        // isLoading={isLoading}
                        pagination={true}
                        // currentPage={currentPage}
                        // setCurrentPage={setCurrentPage}
                        // totalPages={depList?.totalPages}
                        // tableName={""}
                        // onSearch={handleSearch}
                        isHeader={false}
                        tableStyle={{ padding: "0px", boxShadow: "none" }}
                        scopedSlots={createPayRunScopedSlots}
                    />


                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={"Add"}
                            classNames={"btn btn-org-gradient"}
                        // handleClick={() => submitCreateUpdateDepartment()}
                        // isLoading={selectedDepartmentData ? updateDepartmentPending : addDepartmentPending}
                        />
                    </div>
                </div>
            </div>
            {isOpen && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreatePayrun
