import React, { useState } from 'react'
import AttendanceTrackingTable from './AttendanceTrackingTable'
import EmpAttendanceTrackingTable from './EmpAttendanceTrackingTable'
import AttendanceFilters from './AttendanceFilters';
import moment from 'moment';

const AttendanceTracking = (props) => {
    
    const {
        allEmployeeList,
        allDepartments,
        allDesignations,
        handleDepartmentByDesignation,
        getAllEmployeeDetailsByPayDays,
        getEmployeeDetailsByPayDays
    } = props
    const [currentPage, setCurrentPage] = useState(1);
    const [monthYear,setMonthYear]=useState(moment().format("YYYY-MM"))
    return (
        <React.Fragment>
            <div className="d-flex justify-content-between align-items-center mb-2">
                <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Attendance Tracking</h3>
            </div>
            <AttendanceFilters
                allEmployeeList={allEmployeeList}
                allDepartments={allDepartments}
                allDesignations={allDesignations}
                handleDepartmentByDesignation={handleDepartmentByDesignation}
                getEmployeeDetailsByPayDays={getEmployeeDetailsByPayDays}
                setMonthYear={setMonthYear}
            />
            <EmpAttendanceTrackingTable
                setCurrentPage={setCurrentPage}
                currentPage={currentPage}
                allEmployeeData={allEmployeeList}
                getAllEmployeeDetailsByPayDays={getAllEmployeeDetailsByPayDays}
                monthYear={monthYear}

            />
            {/* <AttendanceTrackingTable/> */}
        </React.Fragment>
    )
}

export default AttendanceTracking
