import React, { useMemo, useState } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { CustomButton } from '../../../globalComponents'
import InstallmentTable from './InstallmentTable';
const loan = [
    {
        emp_id: "qd2654",
        name: "Mohd Sarfaraz",
        email: "sarfaraz1@qdegrees.com",
        department: "IT",
        loan_type: "Personal Loan",
        amount: "100",
        emi_duration: "3",
        effective_date: "2024-11-21",
        end_date: "2025-01-21",
    },
    {
        emp_id: "qd2281",
        name: "Chandan Kumar",
        email: "Candan@qdegrees.com",
        department: "IT",
        loan_type: "Car Loan",
        amount: "10000",
        emi_duration: "10",
        effective_date: "2024-12-01",
        end_date: "2025-08-30",
    },

]
const installments = [
    {
        installment: 1,
        date: "2024-11-21",
        amount: "33.33",
    },
    {
        installment: 2,
        date: "2024-12-21",
        amount: "33.33",
    },
    {
        installment: 3,
        date: "2025-01-21",
        amount: "33.33",
    },
];
const Loans = (props) => {
    const { toggleAddLoanModal } = props
    const [installmentData, setInstallmentData] = useState(installments)

    const loanFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'name', label: 'Employee Name' },
        { key: 'emp_id', label: 'Employee ID' },
        { key: 'department', label: 'Department' },
        { key: 'loan_type', label: 'Loan Type' },
        { key: 'loan_amount', label: 'Loan Amount' },
        { key: 'emi_duration', label: 'EMI Duration' },
        { key: 'effective_date', label: 'Effective Date' },
        { key: 'end_date', label: 'End Date' },
        { key: 'action', label: 'Action' },
    ]

    const loanScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                        onClick={() => toggleAddLoanModal(item?._id ,"edit")} 
                        >
                            View  Details</span>
                    </li>

                </ul>
            </div>
        )

    }), []);

    return (
        <div>
            <div className="d-flex justify-content-between align-items-center mb-2">
                <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Loans</h3>
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() => toggleAddLoanModal()}
                    text={"Add Loan"}
                />
            </div>
            <CustomTable
                data={loan}
                fields={loanFields}
                // isLoading={isLoading}
                pagination={true}
                // currentPage={currentPage}
                // setCurrentPage={setCurrentPage}
                // totalPages={depList?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={loanScopedSlots}
            />
            {/* {installmentData && installmentData?.length > 0 &&
                <div className="col-12">
                    <div className='card text-center p-1 m-o' style={{ backgroundColor: "#3c82e9", color: "white" }}> <h6 >Edit Installment </h6></div>
                    <InstallmentTable
                        installmentData={installmentData}
                        setInstallmentData={setInstallmentData}
                        loanData={loan}
                    />
                </div>} */}
        </div>
    )
}

export default Loans
