import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { values } from 'lodash';
const payrollHistoryData = [
    {
      salaryForMonth: "May 2024",
      paymentMonth: "June 2024",
      paymentDate: "2024-06-07",
    },
    {
      salaryForMonth: "June 2024",
      paymentMonth: "July 2024",
      paymentDate: "2024-07-07",
    },
    {
      salaryForMonth: "February 2024",
      paymentMonth: "March 2024",
      paymentDate: "2024-03-07",
    },
    {
      salaryForMonth: "March 2024",
      paymentMonth: "April 2024",
      paymentDate: "2024-04-07",
    },
    {
      salaryForMonth: "April 2024",
      paymentMonth: "May 2024",
      paymentDate: "2024-05-07",
    },
    {
      salaryForMonth: "September 2024",
      paymentMonth: "October 2024",
      paymentDate: "2024-10-07",
    },
  ];
  
const PayrollHistory = () => {

    const payrollHistoryField =[
        {key:"salaryForMonth",label:"Salary For Month"},
        {key:"paymentMonth",label:"Payment Month"},
        {key:"paymentDate",label:"Payment Date"},
        {key:"action",label:"Action"},
    ]
    const payrollHistoryScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                        // onClick={() => toggleAddLoanModal(item?._id ,"edit")} 
                        >
                            View  Details</span>
                    </li>

                </ul>
            </div>
        )

    }), []);

  return (
    <React.Fragment>
         <div className="d-flex justify-content-between align-items-center mb-2">
                <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Payroll History</h3>
                
            </div>
         <CustomTable
                data={payrollHistoryData}
                fields={payrollHistoryField}
                // isLoading={isLoading}
                pagination={true}
                // currentPage={currentPage}
                // setCurrentPage={setCurrentPage}
                // totalPages={depList?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={payrollHistoryScopedSlots}
            />
      
    </React.Fragment>
  )
}

export default PayrollHistory
