import React, { useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { Input, Label } from '../../../commanComponent'
import { useCustomTranslation } from '../../../../translation'
import CustomSelect from '../../../commanComponent/CustomSelect'
import moment from 'moment'
import InstallmentTable from './InstallmentTable'
const durationOptions = [
    { label: "3 Months", value: 3 },
    { label: "4 Months", value: 4 },
    { label: "5 Months", value: 5 },
    { label: "6 Months", value: 6 },
    { label: "7 Months", value: 7 },
    { label: "8 Months", value: 8 },
    { label: "1 Year", value: 12 },
    { label: "2 Years", value: 24 },
    { label: "3 Years", value: 36 },
];
const AddLoans = (props) => {
    const { t } = useCustomTranslation()
    const { toggleOpenClose, isOpen } = props

    const [loanFormData, setLoanFormData] = useState({
        employee: null,
        loanType: null,
        amount: '',
        emiDuration: null,
        effectiveDate: '',
        endDate: '',
    })
    const [errors, setErrors] = useState('')
    const [installmentData, setInstallmentData] = useState([])

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };
        if (name === "effectiveDate") {
            calculateEndDate(value)
        }
        if (name === "emiDuration") {
            setLoanFormData(prevState => ({
                ...prevState,
                ["effectiveDate"]: '',
                ["endDate"]: '',
            }));
            setInstallmentData([])
        }
        if (name === "amount") {
            setLoanFormData(prevState => ({
                ...prevState,
                ["emiDuration"]: null,
                ["effectiveDate"]: '',
                ["endDate"]: '',
            }));
            setInstallmentData([])
        }
        setLoanFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });
        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };
    const calculateEndDate = effectiveDate => {
        var newDate = moment(effectiveDate)
            .add(loanFormData?.emiDuration?.value - 1, 'M')
            .format('YYYY-MM-DD');
        newDate = moment(newDate).format('YYYY-MM-DD');
        setLoanFormData(prevState => {
            const updatedState = {
                ...prevState,
                ["endDate"]: newDate,
            };
            return updatedState;
        });
        calculateEmiInstallment(effectiveDate);
    };

    const calculateEmiInstallment = (effectiveDate) => {
        let duration = parseInt(loanFormData?.emiDuration?.value);
        let emi = parseFloat(loanFormData?.amount / duration).toFixed(2);
        let temp = [];
        let date = moment(effectiveDate).format('YYYY-MM-DD');
        for (let i = 1; i <= duration; i++) {
            temp.push({ installment: i, date: date, amount: emi, });
            let len = temp.length;
            //   subCompanyDetails?.length && subCompanyDetails?.map((item,index)=>{
            //     if(index === 0){
            //     temp[len-1].subcompany.push({[item.value]:100});
            //     }else{
            //       temp[len-1].subcompany.push({[item.value]:0});
            //     }
            //   })
            date = moment(date)
                .add(1, 'month')
                .format('YYYY-MM-DD');
        }
        console.log(temp, "temporary Installment ................................")
        setInstallmentData(temp);
    };




    return (<>
        <div className={`offcanvas offcanvas-end creatNewTicketOff ${isOpen ? 'show' : ''}`} tabIndex={-1}
            style={{
                visibility: isOpen ? 'visible' : 'hidden',
                width: "800px"
            }}
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> Add Loans </h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => toggleOpenClose()}
                />
            </div>
            <div className="offcanvas-body">
                <div className="formPanel">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.employee")} required={true} />
                                <CustomSelect
                                    data={[]}
                                    placeholder={t("placeholders.employee")}
                                    value={loanFormData?.employee || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "employee")}
                                    name="employee"
                                />
                            </div>
                            {errors?.employee && (
                                <CustomError text={errors.employee} />
                            )}
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.loanType")} />
                                <CustomSelect
                                    data={[]}
                                    placeholder={t("placeholders.loanType")}
                                    value={loanFormData?.loanType || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "loanType")}
                                    name="loanType"
                                />
                            </div>
                        </div>

                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.amount")} required={true} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.amount")}
                                    value={loanFormData?.amount}
                                    name="amount"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.amount && (
                                    <CustomError text={errors.amount} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.emiDuration")} required={true} />

                                <CustomSelect
                                    data={durationOptions}
                                    placeholder={t("placeholders.emiDuration")}
                                    value={loanFormData?.emiDuration || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "emiDuration")}
                                    name="emiDuration"
                                />
                            </div>
                            {errors?.emiDuration && (
                                <CustomError text={errors.emiDuration} />
                            )}
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.effectiveDate")} required={true} />
                                <Input
                                    type="date"
                                    value={loanFormData?.effectiveDate}
                                    isDisabled={!(loanFormData?.emiDuration) ? true : false}
                                    name="effectiveDate"
                                    onChange={(e) => handleFromChange(e)}
                                />

                            </div>
                            {errors?.effectiveDate && (
                                <CustomError text={errors.effectiveDate} />
                            )}
                        </div>

                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.endDate")} required={true} />
                                <Input
                                    type="date"
                                    value={loanFormData?.endDate}
                                    isDisabled={true}
                                    name="endDate"
                                    onChange={(e) => handleFromChange(e)}
                                />
                            </div>
                        </div>
                        
                        <hr/>
                       {installmentData &&installmentData?.length>0 &&
                       <div className="col-12">
                            <div className='card text-center p-1 m-o' style={{backgroundColor:"#3c82e9",color:"white"}}> <h6 >Pending Installment </h6></div>
                            <InstallmentTable
                            installmentData={installmentData}
                            setInstallmentData={setInstallmentData}
                            loanData={loanFormData}
                            />
                        </div>}

                    </div>
                </div>

            </div>

            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={"Add"}
                        classNames={"btn btn-org-gradient"}
                    // handleClick={() => submitCreateUpdateDepartment()}
                    // isLoading={selectedDepartmentData ? updateDepartmentPending : addDepartmentPending}
                    />
                </div>
            </div>
        </div>
        {isOpen && <div className="modal-backdrop fade show"></div>}
    </>
    )
}

export default AddLoans
